import React from 'react';

//Graveyard ecosystem logos
import tombLogo from '../../assets/img/stone_c.png';
import tShareLogo from '../../assets/img/sshares_c.png';
import tombLogoPNG from '../../assets/img/crypto_tomb_cash.png';
import tShareLogoPNG from '../../assets/img/crypto_tomb_share.png';
import tBondLogo from '../../assets/img/sbonds_c.png';

import tombFtmLpLogo from '../../assets/img/tomb_ftm_lp.png';
import tshareFtmLpLogo from '../../assets/img/tshare_ftm_lp.png';

import wftmLogo from '../../assets/img/ftm_logo_blue.svg';
import booLogo from '../../assets/img/spooky.png';
import daiLogo from '../../assets/img/DAI.png';
import usdcLogo from '../../assets/img/USDC.png';

const logosBySymbol: { [title: string]: string } = {
  //Real tokens
  //=====================
  STONE: tombLogo,
  TOMBPNG: tombLogoPNG,
  TSHAREPNG: tShareLogoPNG,
  SSHARE: tShareLogo,
  SBOND: tBondLogo,
  WFTM: wftmLogo,
  BOO: booLogo,
  USDC: usdcLogo,
  DAI: daiLogo,
  'STONE-FTM-LP': tombFtmLpLogo,
  'SSHARE-FTM-LP': tshareFtmLpLogo,
};

type LogoProps = {
  symbol: string;
  size?: number;
};

const TokenSymbol: React.FC<LogoProps> = ({ symbol, size = 64 }) => {
  if (!logosBySymbol[symbol]) {
    // throw new Error(`Invalid Token Logo symbol: ${symbol}`);
  }
   return <img src={logosBySymbol[symbol]} alt={`${symbol} Logo`} width={size} height={size} />;
};

export default TokenSymbol;
